'use client';
import { useState } from "react";
import Header from "@/app/components/Header";
import Sidebar from "@/app/components/sidebar";
import Dashboard from '@/app/components/Dashboard';
import QrCodeWhatsApp from '@/app/components/QrCodeWhatsApp';

export default function Painel() {
  const [page, setPage] = useState('dashboard');

  function renderContent() {
    switch (page) {
      case 'dashboard':
        return <Dashboard />;
      case 'qrcode':
        return (
          <div>
            <h2 className="text-2xl font-bold mb-3">Conectar WhatsApp</h2>
            <div className="bg-white shadow rounded-lg p-8 text-center">
              <QrCodeWhatsApp />
              <p className="mt-4 text-gray-600 text-sm">
                Abra o WhatsApp {'>'} Dispositivos conectados {'>'} Escanear QR Code.
              </p>
            </div>
          </div>
        );
      case 'whatsapp':
        return (
          <div>
            <h2 className="text-2xl font-bold mb-3">WhatsApp Web</h2>
            <p className="text-gray-600">
              Interface em construção (em breve: lista de conversas, envio/recebimento, IA...)
            </p>
          </div>
        );
      case 'crm':
        return (
          <div>
            <h2 className="text-2xl font-bold mb-3">CRM de Clientes</h2>
            <p className="text-gray-600">
              Cadastro e gestão de clientes, leads e histórico de conversas.
            </p>
          </div>
        );
      case 'fluxograma':
        return (
          <div>
            <h2 className="text-2xl font-bold mb-3">Editor de Fluxogramas</h2>
            <p className="text-gray-600">
              Construa fluxos de automação visualmente. (React Flow, Drag-and-drop...)
            </p>
          </div>
        );
      case 'config':
        return (
          <div>
            <h2 className="text-2xl font-bold mb-3">Configurações</h2>
            <p className="text-gray-600">
              Ajuste integrações, preferências e dados da conta.
            </p>
          </div>
        );
      default:
        return <div>Bem-vindo ao painel!</div>;
    }
  }

  return (
    <div className="min-h-screen flex flex-col">
      <Header user="Gabriel" onLogout={() => { /* lógica de logout */ }} />
      <div className="flex flex-1">
        <Sidebar current={page} onSelect={setPage} />
        <main className="flex-1 bg-slate-50 p-10 overflow-auto">
          {renderContent()}
        </main>
      </div>
    </div>
  );
}
