'use client'
import { useEffect, useState } from 'react';
import QRCode from 'react-qr-code';

export default function QrCodeWhatsApp() {
  const [qrcode, setQrcode] = useState<string | null>(null);
  const [isConnected, setIsConnected] = useState(false);

  useEffect(() => {
    async function fetchQr() {
      const res = await fetch('http://localhost:3333/qrcode');
      const data = await res.json();
      setQrcode(data.q);
      setIsConnected(data.isConnected);
    }
    fetchQr();

    // Atualiza a cada 7 segundos (QR muda ou pode expirar)
    const interval = setInterval(fetchQr, 7000);
    return () => clearInterval(interval);
  }, []);

  if (isConnected) {
    return <div className="text-green-600 font-bold">WhatsApp conectado!</div>;
  }

  return (
    <div className="flex flex-col items-center gap-4">
      {qrcode ? (
        <>
          <QRCode value={qrcode} size={220} />
          <span className="text-gray-600 text-sm">Escaneie com o WhatsApp {'>'} Dispositivos Conectados</span>
        </>
      ) : (
        <span className="text-gray-500">Carregando QR Code...</span>
      )}
    </div>
  );
}
